import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
import Swal from "sweetalert2";
export default {
  components: {
    AgGridVue
  },
  data() {
    return {
      rowData: {
        mat_code: "",
        mat_name: "",
        unit: "",
        size: "",
        color: "",
        texture: "",
        shape: "",
        safe_inventory: ""
      },
      rowData2: [{
        mat_code: "",
        mat_name: "",
        unit: "",
        size: "",
        color: "",
        texture: "",
        shape: "",
        safe_inventory: ""
      }],
      columnDefs: [{
        field: "mat_code",
        headerName: "자재코드",
        flex: 3
      }, {
        field: "mat_name",
        headerName: "자재명",
        flex: 3
      }, {
        field: "unit",
        headerName: "단위",
        flex: 3
      }, {
        field: "size",
        headerName: "크기",
        flex: 3
      }, {
        field: "color",
        headerName: "색상",
        flex: 3
      }, {
        field: "texture",
        headerName: "재질",
        flex: 3
      }, {
        field: "shape",
        headerName: "모양",
        flex: 3
      }, {
        field: "safe_inventory",
        headerName: "안전 재고",
        flex: 3
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 5,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.matList();
    this.autoMatCode();
  },
  methods: {
    allowOnlyNumber(event) {
      const allowedKeys = ['Backspace', 'Tab', 'ArrowLeft', 'ArrowRight', 'Delete', 'Home', 'End'];
      const isNumberKey = event.key >= '0' && event.key <= '9';
      if (!isNumberKey && !allowedKeys.includes(event.key)) {
        event.preventDefault();
      }
    },
    //자재 카운트
    async autoMatCode() {
      const result = await axios.get("/api/admin/autoMatCode");
      this.rowData.mat_code = result.data[0].mat_code;
    },
    clearForm() {
      this.rowData = {
        mat_code: "",
        mat_name: "",
        unit: "",
        size: "",
        color: "",
        texture: "",
        shape: "",
        safe_inventory: ""
      };
    },
    //조회
    async matList() {
      await axios.get('/api/admin/mat').then(res => {
        console.log(res.data);
        this.rowData2 = res.data;
      });
    },
    //등록
    async addMat() {
      const res = await axios.post('/api/admin/mat', this.rowData).catch(error => {
        console.error("등록 실패:", error);
        Swal.fire("등록 실패", "자재 등록 중 오류가 발생했습니다.", "error");
        return null;
      });
      if (res && res.data && res.data.affectedRows > 0) {
        Swal.fire("등록성공", "정상적으로 등록되었습니다.", "success").then(() => {
          this.matList();
          this.autoMatCode();
          this.clearForm();
        });
      } else if (res) {
        Swal.fire("등록 실패", "등록이 실패하였습니다.", "error");
      }
    },
    //수정
    async updateMat() {
      axios.put('/api/admin/updateMat', this.rowData).then(res => {
        if (res && res.data && res.data.result == true) {
          Swal.fire("수정 완료", "자재정보가 수정되었습니다.", "success").then(() => {
            this.matList();
            this.autoMatCode();
            this.clearForm();
          });
        } else {
          Swal.fire("수정 실패", "자재정보 수정에 실패했습니다.", "error");
        }
      }).catch(error => {
        console.error("수정 실패:", error);
        Swal.fire("오류 발생", "자재정보 수정 중 오류가 발생했습니다.", "error");
      });
    },
    //삭제
    async deleteMat() {
      if (!this.rowData.mat_code) {
        Swal.fire("삭제 불가", "삭제할 자재를 선택하세요.", "warning");
        return;
      }
      Swal.fire({
        title: "정말 삭제하시겠습니까?",
        text: `자재 코드: ${this.rowData.mat_code}`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        cancelButtonColor: "#3085d6",
        confirmButtonText: "삭제",
        cancelButtonText: "취소"
      }).then(confirm => {
        if (confirm.isConfirmed) {
          axios.delete(`/api/admin/delMat/${this.rowData.mat_code}`).then(res => {
            if (res && res.data && res.data.affectedRows > 0) {
              Swal.fire("삭제 완료", "자재정보가 삭제되었습니다.", "success").then(() => {
                this.facList();
                this.clearForm();
              });
            } else {
              Swal.fire("삭제 실패", "자재 삭제에 실패했습니다.", "error");
            }
          }).catch(error => {
            console.error("삭제 오류:", error);
            Swal.fire("오류 발생", "삭제 중 오류가 발생했습니다.", "error");
          });
        }
      });
    }
  }
};